___CSS_LOADER_EXPORT___.push([module.id, `*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  :root{
    --text-color: white;
    --bg-url:url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    --stroke-color: rgba(255, 255, 255, 0.5);
    --surface-color: rgba(255, 255, 255, 0.05);
    --surface-color-hover:rgba(0, 0, 0, 0.02);
    --highlight-color: rgba(255, 255, 255, 0.2);
    --switch-bg-url: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }
  
  .light{
    --text-color: black;
    --bg-url:url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    --stroke-color: rgba(0, 0, 0, 0.5);
    --surface-color: rgba(0, 0, 0, 0.05);
    --surface-color-hover:rgba(0, 0, 0, 0.02);
    --highlight-color: rgba(0, 0, 0, 0.1);
    --switch-bg-url: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  }
  body{
    /*
    background-image: url(assets/bg-mobile.jpg);
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    */
    /*BACKGROUND: COLOR IMAGE REPEAT POSITION /size*/
    background:var(--bg-url) no-repeat top center /cover;
    height: 100vh;
  }
  
  body *{
    font-family: 'Inter', sans-serif;
    color: var(--text-color);
    
  }
  
  #container{
    width: 100%;
    max-width: 588px;
    margin: 56px auto 0px; /*TOP --> RIGHT --> BOTTOM --> LEFT */
    padding: 0 24px;
  }
  
  
  /*PROFILE*/
  
  
  #profile{
    text-align: center;
    padding: 24px;
  }
  
  #profile img{
    display: block;
    width: 190px;
    margin: auto;
  }
  #profile p{
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    margin-top: 8px;
  }
  
  /*SWITCH*/
  
  #switch{
    margin: 4px auto;
  
    /*POSITION PARA SOBREPOSIÇÃO EM DETERMINADO ITEM*/
    position: relative;
    width: 64px;
  }
  
  #switch button{
    width: 32px;
    height: 32px;
    background: white var(--switch-bg-url) no-repeat center;
    border: 0;
    border-radius: 50%;
  
    /*SOBREPOSIÇÃO*/
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    z-index: 1;
  
    animation: slide-back 0.4s;
  }
  
  .light #switch button {
    animation: slide-in 0.4s forwards;
  }
  
  
  #switch button:hover {
    outline: 8px solid var(--highlight-color);
  }
  
  #switch span {
    display: block;
    width: 64px;
    height: 24px;
    background: var(--surface-color);
    border: 1px solid var(--stroke-color);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 9999px;
  
    
  }
  
  
  /*LINKS*/
  
  ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 24px 0;
  
  }
  
  ul li a{
    display: flex;
    align-items: center;
    justify-content: center;
  
    padding: 16px 24px;
  
    background: var(--surface-color);
    border: 1px solid var(--stroke-color);
    border-radius: 8px;
  
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
  
    text-decoration: none;
    font-weight: 500;
  
    transition: background .5s;
  }
  /*PSEUDO-SELECTOR*/
  ul li a:hover{
    background: var(--surface-color-hover);
    border: 1.5px solid var(--text-color);
  }
  
  /*SOCIAL LINKS*/
  #social-links{
    display: flex;
    justify-content: center;
    gap: 16px;
  
    padding: 24px 0;
    font-size: 24px;
  }
  
  #social-links a{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
  
    padding: 16px;
    border-radius: 50%;
    transition: background 0.2s;
  }
  
  #social-links a:hover{
    background: var(--highlight-color);
    
  }
  
  footer{
    padding: 24px;
    text-align: center;
    font-size: 14px;
  }
  
  
  /*MEDIA QUERIES*/
  
  @media (min-width: 700px){
    :root {
      --bg-url:url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    }
  
    .light{
      --bg-url:url(${___CSS_LOADER_URL_REPLACEMENT_5___});
    }
  }
  
  /* --ANIMAÇÕES-- */
  
  @keyframes slide-in{
    from {
      left: 0;
    }
    to {
      left: 50%;
    }
  }
  
  @keyframes slide-back{
    from{
      left: 50%;
    }
    to {
      left: 0;
    }
  }`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;EACxB;;EAEA;IACE,mBAAmB;IACnB,gDAA8B;IAC9B,wCAAwC;IACxC,0CAA0C;IAC1C,yCAAyC;IACzC,2CAA2C;IAC3C,wDAA4C;EAC9C;;EAEA;IACE,mBAAmB;IACnB,gDAAgC;IAChC,kCAAkC;IAClC,oCAAoC;IACpC,yCAAyC;IACzC,qCAAqC;IACrC,wDAAqC;EACvC;EACA;IACE;;;;;KAKC;IACD,gDAAgD;IAChD,oDAAoD;IACpD,aAAa;EACf;;EAEA;IACE,gCAAgC;IAChC,wBAAwB;;EAE1B;;EAEA;IACE,WAAW;IACX,gBAAgB;IAChB,qBAAqB,EAAE,qCAAqC;IAC5D,eAAe;EACjB;;;EAGA,UAAU;;;EAGV;IACE,kBAAkB;IAClB,aAAa;EACf;;EAEA;IACE,cAAc;IACd,YAAY;IACZ,YAAY;EACd;EACA;IACE,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,eAAe;EACjB;;EAEA,SAAS;;EAET;IACE,gBAAgB;;IAEhB,iDAAiD;IACjD,kBAAkB;IAClB,WAAW;EACb;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,uDAAuD;IACvD,SAAS;IACT,kBAAkB;;IAElB,eAAe;IACf,kBAAkB;IAClB,QAAQ;IACR,2BAA2B;IAC3B,OAAO;IACP,UAAU;;IAEV,0BAA0B;EAC5B;;EAEA;IACE,iCAAiC;EACnC;;;EAGA;IACE,yCAAyC;EAC3C;;EAEA;IACE,cAAc;IACd,WAAW;IACX,YAAY;IACZ,gCAAgC;IAChC,qCAAqC;IACrC,kCAAkC;IAClC,qBAAqB;;;EAGvB;;;EAGA,QAAQ;;EAER;IACE,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,eAAe;;EAEjB;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,uBAAuB;;IAEvB,kBAAkB;;IAElB,gCAAgC;IAChC,qCAAqC;IACrC,kBAAkB;;IAElB,0BAA0B;IAC1B,kCAAkC;;IAElC,qBAAqB;IACrB,gBAAgB;;IAEhB,0BAA0B;EAC5B;EACA,kBAAkB;EAClB;IACE,sCAAsC;IACtC,qCAAqC;EACvC;;EAEA,eAAe;EACf;IACE,aAAa;IACb,uBAAuB;IACvB,SAAS;;IAET,eAAe;IACf,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;;IAEb,aAAa;IACb,kBAAkB;IAClB,2BAA2B;EAC7B;;EAEA;IACE,kCAAkC;;EAEpC;;EAEA;IACE,aAAa;IACb,kBAAkB;IAClB,eAAe;EACjB;;;EAGA,gBAAgB;;EAEhB;IACE;MACE,gDAA+B;IACjC;;IAEA;MACE,gDAA+B;IACjC;EACF;;EAEA,kBAAkB;;EAElB;IACE;MACE,OAAO;IACT;IACA;MACE,SAAS;IACX;EACF;;EAEA;IACE;MACE,SAAS;IACX;IACA;MACE,OAAO;IACT;EACF","sourcesContent":["*{\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n  }\r\n  \r\n  :root{\r\n    --text-color: white;\r\n    --bg-url:url(../imgs/blue.jpg);\r\n    --stroke-color: rgba(255, 255, 255, 0.5);\r\n    --surface-col